import React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"
import "../styles/404.css"
import { useBreakpoint } from "gatsby-plugin-breakpoints"

export default function NotFound() {
    const breakpoints = useBreakpoint();

    return (
        <Layout>
            <main>
                {breakpoints.mobile ? 
                <div className='mobile_content'> 
                    <h1 className='mobile_title'>SORRY, PAGE NOT FOUND</h1>
                    <p className='mobile_content_center'><Link to="/">🐴トップページへ🐴</Link></p>
                </div>
                 : null}
                {breakpoints.pc ? 
                <div className='pc_content'> 
                    <h1 className='pc_title'>SORRY, PAGE NOT FOUND</h1>
                    <p className='pc_content_center'><Link to="/">🐴トップページへ🐴</Link></p>
                </div>
                : null}
            </main>

        </Layout>
    )
}